import React from "react";
import "../css/heading.css";
import Programmer from "../images/htmlcss.svg";

const Heading = () => {
  return (
    <div className="div-divider pb-5">
      <div className="container-xxl">
        <div className="row">
          <div className="col-sm-12 col-md-12 col-lg-6">
            <h1 className="name-size">
              Hi! I'm
              <span style={{ color: "#AE45FF" }}> ABHINN KRISHN</span>, your
              Programing Friend.
            </h1>
            <h5 className="text-muted text-center py-1">
              I Design and Develop Websites.
            </h5>
            <h5 className="text-center">
              I expertise in User Interface and User Exerience, Search Engine
              Optimization. My goal is to deliver a fast and secure Website to
              my client.{" "}
            </h5>
          </div>
          <div className="col-sm-12 col-md-12 col-lg-6">
            <img src={Programmer} alt="Programmer" className="programmer-svg" />
          </div>
          <div className="col-sm-12 col-md-6 col-lg-6 contact-btn">
            <button className="btn btn-lg">Contact Me</button>
          </div>
          <div className="col-6 col-sm-12 col-md-6 col-lg-6"></div>
        </div>
      </div>
    </div>
  );
};

export default Heading;
